.tags{list-style:none;margin:0;overflow:hidden;padding:5px 0 0 0}.tags li{float:left}.tags .tag{display:inline-block;padding:10px 20px;background:linear-gradient(45deg, #007bff, #00cccc);color:#fff;border-radius:25px;box-shadow:0px 4px 6px rgba(0,0,0,.1);transition:transform .3s ease,box-shadow .3s ease;position:relative;overflow:hidden;margin:0 10px 0px 0;font-weight:600}.tags .tag::before{content:"";position:absolute;top:0;left:-50%;width:200%;height:100%;background:rgba(255,255,255,.2);transition:transform .6s ease;transform:translateX(-30%) skewX(45deg)}.tags .tag:hover::before{transform:translateX(30%) skewX(45deg)}.tags .tag::after{content:"â€¢";position:absolute;top:50%;right:10px;transform:translateY(-50%);color:rgba(255,255,255,.5);font-size:40px;line-height:1;animation:pulse 2s infinite}@keyframes pulse{0%{transform:scale(1)}50%{transform:scale(1.2)}100%{transform:scale(1)}}.tags .tag:hover{transform:translateY(-2px);box-shadow:0px 6px 10px rgba(0,0,0,.2)}